import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from "app/page-template/home/home.component";
import { AboutComponent } from "app/page-template/about/about.component";
import { ContactUsComponent } from "app/page-template/contact-us/contact-us.component";
import { PracticesComponent } from "app/page-template/practices/practices.component";
import { RequerdesComponent } from "app/page-template/requerdes/requerdes.component";
import { MapsComponent } from "app/page-template/maps/maps.component";


 const routes: Routes = [       
    { path: '', component: HomeComponent },
    { path: 'about', component: AboutComponent },
    { path: 'contact-us', component: ContactUsComponent },
    { path: 'practices', component: PracticesComponent },
    { path: 'requerdes', component: RequerdesComponent },
    { path: 'maps', component: MapsComponent },
  
    { path: '**', redirectTo: '' }
  
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
